@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
.navbar{background-color:#fff}.navbar a{color:#000}.navbar a>img{height:40px;margin:0 40px}.navbar .nav-item{color:#000;font-weight:bold;font-size:18px}.navbar .nav-item .active{color:#df5646}.navbar .nav-item:hover{color:#df5646;cursor:pointer;transition:0.3s}.navbar .social-media-icons{font-size:30px}

#header-section{height:40vw}#header-section .title{font-family:'PT Serif', serif;font-size:71px}#header-section .title .name-color{color:#df5646}#header-section .job-title{font-size:29px;font-weight:500;font-family:'Noto Sans JP', sans-serif;color:#78add9}#header-section .phrase{color:grey}

.about{background:linear-gradient(308deg, rgba(0,0,0,0) 900px, rgba(0,0,0,0) 200px, #DF6C4F 900px, #DF6C4F 900px)}.about_title{padding:20px 0;color:#78add9;font-size:100px}.about_avatar{height:200px;width:200px;border-radius:50%;border:7px solid #78add9}

.contact .img-side{background-color:#df5646}

footer a{padding:0 10px;color:#000;font-size:30px}footer a:hover{color:#df5646}footer p{font-weight:600;font-size:14px}footer button{background:transparent}

body{margin:0;padding-top:100px;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}a{text-decoration:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

